<div class="test">{{msg}}</div><div class="inputBox"><input class="inputSomeThings" v-model="inputContent" v-on:keyup.enter="addSomeThingInList"><button @click="addSomeThingInList">add</button></div><div class="checkList"><div class="eachItem" v-for="(item,index) in checkList"><input type="checkbox" v-model="item.isCheck"><input v-if="item.isEdit" v-model="item.content" v-on:keyup.enter="(item.isEdit &amp;&amp; item.content) ? item.isEdit = !item.isEdit : item.isEdit"><span v-else>{{ item.content }}</span><div class="actButton"><div class="edit" @click="editItem(item)">Edit</div><div class="delete" @click="deleteItem(index)">Delete</div></div></div></div>